import React, { useContext, useEffect, useState } from "react";
import { callApi } from "../../utils/ApiUtils";
import { BotPreview } from "./BotPreview/BotPreview";
import InfiniteScroll from "react-infinite-scroll-component";
import "./BotsList.css";
import { FiltersContext } from "../../App";
import { Spinner } from "react-bootstrap";

export const BotsList = () => {
  const { filtersState } = useContext(FiltersContext);
  const limit = 10;
  const [bots, setBots] = useState<Array<any>>([]);
  const [offset, setOffset] = useState(0);
  const [hasMore, setHasMore] = useState(true);

  const getBotsUrl = (resetOffset = false) =>
    `/public/api/ver1/bots?offset=${resetOffset ? 0 : offset}&limit=${limit}${
      filtersState.scope ? `&scope=${filtersState.scope}` : ""
    }${filtersState.type ? `&strategy=${filtersState.type}` : ""}`;

  const getMore = () => {
    return callApi("GET", getBotsUrl())
      .then((response) => response.json())
      .then((data: Array<any>) => {
        if (data.length === 0) {
          setHasMore(false);
        }
        setOffset((os) => os + limit);
        setBots((bots) => [...bots, ...data]);
      });
  };

  useEffect(() => {
    setHasMore(true);
    callApi("GET", getBotsUrl(true))
      .then((response) => response.json())
      .then((data: Array<any>) => {
        setBots(data);
        setOffset(limit);
        if (data.length < limit) {
          setHasMore(false);
        }
      });
  }, [filtersState]);

  return (
    <InfiniteScroll
      dataLength={bots.length} //This is important field to render the next data
      next={getMore}
      hasMore={hasMore}
      loader={
        <div className="SpinnerContainer">
          <Spinner animation="grow" />
        </div>
      }
      endMessage={
        <p style={{ textAlign: "center" }}>
          <b>Yay! You have seen it all</b>
        </p>
      }
    >
      <div className="BotsList">
        {bots.map((bot: any) => (
          <BotPreview botItem={bot} key={bot.id} />
        ))}
      </div>
    </InfiniteScroll>
  );
};
