import CryptoJS from "crypto-js";

export function getSig(path: string, secret: string) {
  return CryptoJS.algo.HMAC.create(CryptoJS.algo.SHA256, secret)
    .update(path)
    .finalize()
    .toString(CryptoJS.enc.Hex);
}
export const callApi = (method: string, path: string) => {
  const API_KEY =
    new URLSearchParams(window.location.search).get("api_key") || "";
  const SECRET =
    new URLSearchParams(window.location.search).get("secret") || "";

  return fetch(`https://api.3commas.io${path}`, {
    method: method,
    headers: {
      APIKEY: API_KEY,
      Signature: getSig(path, SECRET),
    },
  });
};
